<script lang="ts">
    import {translator} from "../i18n/translator";
    import {slide} from 'svelte/transition';

    export let text = '';

    let isOpen: boolean = false;
    const loadButtonText = () => isOpen ? translator().global.seeLess : translator().global.seeMore;

    let buttonText = loadButtonText();

    const handleClick = () => {
        isOpen = isOpen = !isOpen
        buttonText = loadButtonText();
    }
</script>

{#if isOpen}
    <div transition:slide>
        {@html text}
        <slot></slot>
    </div>
{/if}

<button on:click={handleClick}>{buttonText}</button>

<style>
    button {
        margin-top: 1.5rem;
        -webkit-appearance: none;
        border: 0;
        padding: 0;
        background-color: transparent;
        font-size: 1rem;
        color: #272445;
        cursor: pointer;
        display: block;
        margin-left: auto;
    }

    div :global(a) {
        color: #00AD93;
        text-decoration: none;
    }
</style>
