<script lang="ts">
    import {translator} from "../i18n/translator";
    import {currentStep, form} from "../store";
    import CountStep from "../shared/CountStep.svelte";
    import Error from "../shared/Error.svelte";

    let isSubmit: boolean = false;

    const handleSubmit = () => {
        isSubmit = true;

        if (!$form.countTerminal || !$form.countPoint) {
            return;
        }

        currentStep.set(2);
    }
</script>

<div class="mb-36 heading-md">{translator().step1.heading}</div>

<div class="mb-48 mb-lg-64 svelte-grid">
    <label>
        <span class="mb-08 label">{translator().step1.label1}</span>
        <input bind:value={$form.countTerminal} type="number" class="input-number" placeholder={translator().step1.placeholder1} min="1">
        {#if isSubmit && !$form.countTerminal}
            <Error message={translator().step1.error1}/>
        {/if}
    </label>

    <label>
        <span class="mb-08 label">{translator().step1.label2}</span>
        <input bind:value={$form.countPoint} type="number" class="input-number" placeholder={translator().step1.placeholder2} min="1">
        {#if isSubmit && !$form.countPoint}
            <Error message={translator().step1.error2}/>
        {/if}
    </label>
</div>

<div class="svelte-footer">
    <button class="mb-24 mb-lg-0 button-solid" on:click={handleSubmit}>{translator().global.next}</button>
    <div class="text-align-center">
        <CountStep/>
    </div>
</div>

<style>
    .svelte-grid {
        display: grid;
        grid-gap: 1.5rem;
    }

    .svelte-footer {
        display: flex;
        flex-direction: column;
    }

    .button-solid {
        margin-left: auto;
        margin-right: auto;
    }

    @media (min-width: 992px) {
        .svelte-grid {
            grid-template-columns: 1fr 1fr;
        }

        .svelte-footer {
            flex-direction: row-reverse;
            justify-content: space-between;
            align-items: center;
        }

        .button-solid {
            margin: 0;
        }
    }

</style>
