<script lang="ts">
    import {translator} from "../i18n/translator";
    import {currentStep, form} from "../store";
    import CountStep from "../shared/CountStep.svelte";
    import Prev from "../shared/Prev.svelte";
    import Error from "../shared/Error.svelte";
    import Select from "../shared/Select.svelte";
    import {regionData, sectorData, turnoverData} from "../form.data";
    import {isValidNip} from "../validators/nip";

    let isSubmit: boolean = false;

    const handleSubmit = () => {
        isSubmit = true;

        if (!$form.location || !$form.region || !$form.sector || !$form.turnover || !$form.companyName || !$form.taxId) {
            return;
        }

        if (!isValidNip($form.taxId)) {
            return;
        }

        currentStep.set(3);
    }
</script>

<div class="mb-36 heading-md">{translator().step2.heading}</div>

<div class="mb-48 mb-lg-64 svelte-grid">
    <label>
        <span class="mb-08 label">{translator().step2.labelTaxId}</span>
        <input bind:value={$form.taxId} type="text" class="input-text" placeholder={translator().step2.placeholderTaxId}>
        {#if isSubmit && !$form.taxId}
            <Error message={translator().step2.errorTaxId}/>
        {:else if isSubmit && !isValidNip($form.taxId)}
            <Error message={translator().step2.errorIncorrectTaxId}/>
        {/if}
    </label>

    <label>
        <span class="mb-08 label">{translator().step2.labelCompanyName}</span>
        <input bind:value={$form.companyName} type="text" class="input-text" placeholder={translator().step2.placeholderCompanyName}>
        {#if isSubmit && !$form.companyName}
            <Error message={translator().step2.errorCompanyName}/>
        {/if}
    </label>

    <label>
        <span class="mb-08 label">{translator().step2.labelTurnover}</span>
        <Select bind:value={$form.turnover} items={turnoverData} placeholder="{translator().step2.placeholderTurnover}"/>
        {#if isSubmit && !$form.turnover}
            <Error message={translator().step2.errorTurnover}/>
        {/if}
    </label>

    <label>
        <span class="mb-08 label">{translator().step2.labelSector}</span>
        <Select bind:value={$form.sector} items={sectorData} placeholder="{translator().step2.placeholderSector}"/>
        {#if isSubmit && !$form.sector}
            <Error message={translator().step2.errorSector}/>
        {/if}
    </label>

    <label>
        <span class="mb-08 label">{translator().step2.labelRegion}</span>
        <Select bind:value={$form.region} items={regionData} placeholder="{translator().step2.placeholderRegion}"/>
        {#if isSubmit && !$form.region}
            <Error message={translator().step2.errorRegion}/>
        {/if}
    </label>

    <label>
        <span class="mb-08 label">{translator().step2.label6}</span>
        <input bind:value={$form.location} type="text" class="input-text" placeholder={translator().step2.placeholder6}>
        {#if isSubmit && !$form.location}
            <Error message={translator().step2.errorLocation}/>
        {/if}
    </label>
</div>

<div class="svelte-footer text-align-center">
    <div class="svelte-footer__card">
        <div class="mb-24 mb-lg-0">
            <Prev/>
        </div>
        <button class="mb-24 button-solid" on:click={handleSubmit}>{translator().global.next}</button>
    </div>
    <CountStep/>
</div>

<style>
    .svelte-grid {
        display: grid;
        grid-gap: 1.5rem;
    }

    .svelte-footer {
        display: flex;
        flex-direction: column;
    }

    .button-solid {
        margin-left: auto;
        margin-right: auto;
    }

    @media (min-width: 992px) {

        .svelte-grid {
            grid-template-columns: 1fr 1fr;
        }

        .svelte-footer {
            flex-direction: row-reverse;
            justify-content: space-between;
            align-items: center;
        }

        .svelte-footer__card {
            display: flex;
            align-items: center;
            grid-gap: 1.5rem;
        }

        .button-solid {
            margin: 0;
        }
    }
</style>
