<script lang="ts">
    import {translator} from "../i18n/translator";
    import {currentStep, form} from "../store";
    import CountStep from "../shared/CountStep.svelte";
    import Prev from "../shared/Prev.svelte";
    import Error from "../shared/Error.svelte";
    import Accordion from "../shared/Accordion.svelte";
    import InputCheckbox from "../shared/InputCheckbox.svelte";
    import {isValidEmail} from "../validators/email";
    import {createOrder} from "../api/createOrder";

    let isSubmit: boolean = false;
    let isActiveRequest = false;
    let isErrorRequest = false;

    const handleSubmit = () => {
        isSubmit = true;

        if (!$form.fullName || !$form.email || !$form.term1) {
            return;
        }

        if (!isValidEmail($form.email)) {
            return;
        }

        isActiveRequest = true;

        createOrder($form)
            .then(response => {
                if (response.ok) {
                    currentStep.set(4);
                }
            })
            .catch(() => isErrorRequest = true);
    }
</script>

<div class="mb-36 heading-md">{translator().step3.heading}</div>

<div class:isActiveRequest>
    <div class="mb-48 svelte-grid">
        <label>
            <span class="mb-08 label">{translator().step3.label1}</span>
            <input bind:value={$form.fullName} type="text" class="input-text" placeholder={translator().step3.placeholder1}>
            {#if isSubmit && !$form.fullName}
                <Error message={translator().step3.error1}/>
            {/if}
        </label>

        <label>
            <span class="mb-08 label">{translator().step3.label2}</span>
            <input bind:value={$form.mobileNo} type="text" class="input-text" placeholder={translator().step3.placeholder2}>
        </label>

        <label>
            <span class="mb-08 label">{translator().step3.label3}</span>
            <input bind:value={$form.email} type="text" class="input-text" placeholder={translator().step3.placeholder3}>
            {#if isSubmit && !$form.email}
                <Error message={translator().step3.error3}/>
            {:else if isSubmit && !isValidEmail($form.email)}
                <Error message={translator().step3.errorIncorrectEmail}/>
            {/if}
        </label>
    </div>

    <div class="mb-48 mb-lg-64">
        <div class="mb-24">
            <InputCheckbox bind:checked={$form.term1}>{translator().step3.term1}</InputCheckbox>
            {#if isSubmit && !$form.term1}
                <Error message={translator().step3.term1Error}/>
            {/if}
        </div>

        <div class="mb-24">
            <div>{translator().step3.privacyShort}</div>
            <Accordion text="{translator().step3.privacyLong}"></Accordion>
        </div>

    </div>

    <div class="svelte-footer text-align-center">
        <div class="svelte-footer__card">
            <div class="mb-24 mb-lg-0">
                <Prev/>
            </div>
            <div>
                <button class="mb-24 button-solid" disabled={isActiveRequest} on:click={handleSubmit}>{translator().global.createOrder}</button>
                <div hidden={!isErrorRequest}>
                    <Error message="Something wrong. Please try again later!"/>
                </div>
            </div>
        </div>
        <CountStep/>
    </div>
</div>

<style>
    .isActiveRequest {
        cursor: not-allowed;
        opacity: .5;
    }

    .svelte-grid {
        display: grid;
        grid-gap: 1.5rem;
    }

    .svelte-footer {
        display: flex;
        flex-direction: column;
    }

    .button-solid {
        margin-left: auto;
        margin-right: auto;
    }

    @media (min-width: 992px) {
        .svelte-footer {
            flex-direction: row-reverse;
            justify-content: space-between;
            align-items: center;
        }

        .svelte-footer__card {
            display: flex;
            align-items: center;
            grid-gap: 1.5rem;
        }

        .button-solid {
            margin: 0;
        }
    }
</style>
