<script lang="ts">
    import {clickOutside} from "../functions/clickOutside";
    import type {Select} from "../form.data";

    export let value;
    export let items: Select[];
    export let placeholder;
    let open = false;
    let label = items.find(item => item.value === value)?.label;

    const handleClick = (event: Event) => {
        event.preventDefault();
        open = !open;
    };
    const setValue = (item) => {
        value = item.value;
        label = item.label;

        items = [...items];
    };

    const selected = (item) => item.value === value;
    const close = () => open = false;
</script>


<div class="custom-select-wrapper" on:click={handleClick} use:clickOutside on:clickOutside={() => close()}>
    <div class="custom-select" class:open>
        <div class="custom-select__trigger custom-select__trigger--placeholder custom-select__trigger--with-triangle">
            <input bind:value={label} class="custom-select__input-search" placeholder={placeholder} readonly>
            <div class="triangle triangle--down"></div>
        </div>
        <div class="custom-select__options">
            {#each items as item}
                <div class="custom-select__option"
                     on:click={() => setValue(item)}
                     class:selected={selected(item)}
                >{item.label}</div>
            {/each}
        </div>
    </div>
</div>
