const nodeSiteUrl = document.querySelector('#siteUrl');
export const apiUrl = (nodeSiteUrl ? nodeSiteUrl.value : 'http://payeye2.0.local') + '/wp-json/payeye/v1';
export const createOrder = (data) => {
    const headers = new Headers();
    headers.append("Content-Type", "application/json");
    return fetch(`${apiUrl}/orders`, {
        method: 'POST',
        headers: headers,
        body: JSON.stringify(data),
        redirect: 'follow'
    });
};
