<script lang="ts">
    import {currentStep} from "../store";
    import {translator} from "../i18n/translator";

    const handlePrev = (e: Event) => {
        e.preventDefault();
        currentStep.update(n => n - 1);
    }
</script>

<a href="#" class="heading-xs" on:click={handlePrev}>{translator().global.prev}</a>

<style>
    a {
        text-decoration: none;
        text-align: center;
        display: block;
    }
</style>
